// ============================================ *
// * Variables
// ============================================ *
@import "bourbon/bourbon"; // Bourbon scss mixins
@import "mixins"; // Custom mixins, functions, etc

// Borders
$border-radius: 5px;

// Sizes
$max-width: 1280px;
$wide: 1223px;
$desktop: 998px;
$tablet: 767px;
$mobile: 482px;

// Colors
$c-purple: #B289EF;
$c-darkPurple: #966df3;

$c-pink: #FF3D6A;
$c-green: #2fec94;

$c-gray: #474747;
$c-lightGray: #a5a6a8;
$c-teal: #47c5ca;
$c-blue: #4da1ff;
$c-darkBlue: #273037;
$c-red: #f07575;
$c-yellow: #FFD012;

// New Colors
$c-text: #595D66;
$c-textLight: #989898;
$c-textDark: #4A4A4A;
$c-textDarkBlue: #323C47;

$c-border: #E9EFF4;
$c-grayBackground: #f8f9fd;

$c-offWhite: #f3f3f3;
$c-white: #ffffff;
$c-warning: #ffd012;
$c-danger: #f07575;
$c-success: #a6e50f;
$c-info: #4da1ff;
$c-moss: #637c8a;


// Typography
@include font-face("OpenSans-Light", "../fonts/OpenSans-Light", $file-formats: ttf woff);
@include font-face("OpenSans-LightItalic", "../fonts/OpenSans-LightItalic", $file-formats: ttf woff);
@include font-face("OpenSans-Regular", "../fonts/OpenSans-Regular", $file-formats: ttf woff);
@include font-face("OpenSans-Italic", "../fonts/OpenSans-Italic", $file-formats: ttf woff);
@include font-face("OpenSans-SemiBold", "../fonts/OpenSans-SemiBold", $file-formats: ttf woff);
@include font-face("OpenSans-SemiBoldItalic", "../fonts/OpenSans-SemiBoldItalic", $file-formats: ttf woff);
@include font-face("OpenSans-Bold", "../fonts/OpenSans-Bold", $file-formats: ttf woff);
@include font-face("OpenSans-BoldItalic", "../fonts/OpenSans-BoldItalic", $file-formats: ttf woff);

$font-light: "OpenSans-Light", "Helvetica", sans-serif;
$font-lightItalic: "OpenSans-LightItalic", "Helvetica", sans-serif;
$font-regular: "OpenSans-Regular", "Helvetica", sans-serif;
$font-italic: "OpenSans-Italic", "Helvetica", sans-serif;
$font-semiBold: "OpenSans-SemiBold", "Helvetica", sans-serif;
$font-semiboldItalic: "OpenSans-SemiBoldItalic", "Helvetica", sans-serif;
$font-bold: "OpenSans-Bold", "Helvetica", sans-serif;
$font-boldItalic: "OpenSans-BoldItalic", "Helvetica", sans-serif;

$letter-spacing: .04em;
$font-opensans: $font-regular;
$font-roboto: $font-regular;



