.qarr .utility-info .license-status {
  display: flex;
  align-items: center;
}

.qarr .utility-info .license-status h2, .qarr .utility-info .license-status p {
  line-height: normal;
}

.qarr .utility-info .license-status h2 {
  margin: 0 0 4px 0;
}

.qarr .utility-info .license-status p {
  margin: 0;
}

.qarr .utility-info .utility-icon {
  margin-left: auto;
  font-size: 48px;
}

.qarr .utility-info .utility-icon.license-status-invalid {
  color: #f07575;
}

.qarr .utility-info .utility-icon.license-status-valid {
  color: #2fec94;
}

.qarr .utility-info .utility-icon.license-status-mismatched {
  color: #a5a6a8;
}



